import React, { Component } from "react";
import WlListItem from "../wl-list-item";
import { connect } from "react-redux";

import CartTop from "../cart-top";
import AddWl from "../add-wl";
import { withWorklogstoreService } from "../hoc";
import {
  fetchworklogs,
  worklogAddedToCart,
  worklogRemoved,
  worklogCopy,
  worklogDownload,
  worklogFavorite,
} from "../../actions";
import { compose } from "../../utils";
import buttonAdd from "../../img/plus.svg";
import Spinner from "../spinner";
import ErrorIndicator from "../error-indicator";
// import AddWl from "../add-wl";
import "./wl-list.css";

const WlList = ({
  worklogs,
  onAddedToCart,
  onDelete,
  onCopy,
  onDownload,
  onFavorite,
 
  onStart,
}) => {
  let date = new Date();
  let nowDate = {
    y: date.getFullYear(),
    m: +(date.getMonth() + 1),
    d: date.getDate(),
    h: date.getHours(),
    min: date.getMinutes(),
    s: date.getSeconds(),
    ms: date.getMilliseconds(),
  };
  return (
    <div className="cart-day">
      <div className="cart">
        <div className="cart-content">
          {worklogs.map((worklog) => {
            if (worklog.startedAt > 0 && worklog.isStarting !== true) {
              return (
                <WlListItem
                  key={worklog.id}
                  id={worklog.id}
                  worklog={worklog}
                  onAddedToCart={() => onAddedToCart(worklog.id)}
                  onDelete={() => onDelete(worklog.id)}
                  onCopy={() => onCopy(worklog.id, nowDate)}
                  onDownload={() => onDownload(worklog.id)}
                  onFavorite={() => onFavorite(worklog.id)}
                />
              );
            }
            if (worklog.timeStart === "") {
              return (
                <WlListItem
                  key={worklog.id}
                  id={worklog.id}
                  worklog={worklog}
                  onAddedToCart={() => onAddedToCart(worklog.id)}
                  onDelete={() => onDelete(worklog.id)}
                  onCopy={() => onCopy(worklog.id, nowDate)}
                  onStart={() => onStart(worklog.id, nowDate)}
                  onDownload={() => onDownload(worklog.id)}
                  onFavorite={() => onFavorite(worklog.id)}
                />
              );
            }

            return (
              <WlListItem
                key={worklog.id}
                id={worklog.id}
                worklog={worklog}
                onAddedToCart={() => onAddedToCart(worklog.id)}
                onDelete={() => onDelete(worklog.id)}
                onCopy={() => onCopy(worklog.id, nowDate)}
                onDownload={() => onDownload(worklog.id)}
                onFavorite={() => onFavorite(worklog.id)}
              />
            );
          })}
        </div>
      </div>
    </div>
  );
};

class WlListContainer extends Component {
  componentDidMount() {
    // здесь наши пропсы перетекают в один сплошной пропс дабы не маячить в коде
    this.props.fetchworklogs();
  }

  render() {
    const {
      worklogs,
      loading,
      error,
      modalIsOpen,
      onAddedToCart,
      onDelete,
      onCopy,
     
      onDownload,
      onFavorite,
      showFavoriteList,
      favorites,
    } = this.props;

    if (loading) {
      return <Spinner />;
    }

    if (error) {
      return <ErrorIndicator />;
    }
    if (modalIsOpen) {
      return (
        <div>
          <CartTop />
          <WlList worklogs={worklogs} />
          <AddWl />
        </div>
      );
    }

    if (showFavoriteList) {
      if (favorites.length < 1) {
        return (
          <div>
            <div className="empty">There is no favorite issues yet</div>
            <div className="">
              <div className="add">
                <img
                  src={buttonAdd}
                  alt="icon"
                  // onClick={onShowModal}
                />
              </div>
            </div>
          </div>
        );
      } else {
        return (
          <div>
            <WlList worklogs={favorites} />
          </div>
        );
      }
    }
    return (
      <div>
        <CartTop />
        <WlList
          worklogs={worklogs}
          onAddedToCart={onAddedToCart}
          onDelete={onDelete}
          onCopy={onCopy}
          onDownload={onDownload}
          onFavorite={onFavorite}
        />
      </div>
    );
  }
}

const mapStateToProps = ({
  worklogs,
  seconds,
  loading,
  error,
  showFavoriteList,
  favorites,
  modalIsOpen,
}) => {
  return {
    worklogs,
    seconds,
    loading,
    error,
    modalIsOpen,
    showFavoriteList,
    favorites,
  };
};

const mapDispatchToProps = (dispatch, ownProps) => {
  const { worklogstoreService } = ownProps;
  return {
    fetchworklogs: fetchworklogs(worklogstoreService, dispatch),
    onAddedToCart: (id) => dispatch(worklogAddedToCart(id)),
    onDelete: (id) => dispatch(worklogRemoved(id)),
    onCopy: (id, worklogDate) => dispatch(worklogCopy(id, worklogDate)),
    onDownload: (id) => dispatch(worklogDownload(id)),
    onFavorite: (id) => dispatch(worklogFavorite(id)),
  };
};

export default compose(
  withWorklogstoreService(),
  connect(mapStateToProps, mapDispatchToProps)
)(WlListContainer);
